html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
  color: white;
}

h1, h2 {
  margin: 0;
  padding: 0;
  font-family: "Fraunces", serif;
  font-weight: 900;
}

.container-main .article {
  width: 90%;
  margin: 5rem auto;
  text-align: center;
}
.container-main .article h2 {
  font-size: 4rem;
}
.container-main .article p {
  color: #a7abae;
  line-height: 4rem;
}
.container-main img {
  width: 100%;
  height: 100%;
}
@media (min-width: 992px) {
  .container-main {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
  .container-main .article {
    width: 60%;
    text-align: left;
  }
  .container-main .transform-imagen {
    grid-column: 2/3;
  }
  .container-main .transform-text {
    grid-row: 1/2;
  }
}

.learn-more {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.learn-more a {
  font-family: "Fraunces", serif;
  font-weight: 900;
  color: black;
  text-transform: uppercase;
  text-align: left;
  margin: 0 0 -1.6rem 0.2rem;
  z-index: 1;
}
.learn-more .sub {
  width: 13rem;
  border: 0.5rem solid #FCF9BD;
  border-radius: 2rem;
}
.learn-more .sub-desktop {
  display: none;
}
.learn-more:hover .sub {
  border-color: #fad400;
}
@media (min-width: 992px) {
  .learn-more {
    align-items: flex-start;
  }
  .learn-more .sub-desktop {
    display: initial;
  }
  .learn-more .sub-mobile {
    display: none;
  }
}

.learn-more-red .sub {
  border-color: #FED9D3;
}
.learn-more-red:hover .sub {
  border-color: #FE7563;
}

.content-middle {
  text-align: center;
}
.content-middle h3 {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 4rem;
  margin-bottom: 0;
}
.content-middle .article {
  margin: 0 auto 4rem;
  align-self: flex-end;
}
.content-middle .graphic-design,
.content-middle .photography {
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: center;
  display: flex;
  min-height: 60rem;
}
.content-middle .graphic-design {
  background-image: url(/build/img/desktop/image-graphic-design.webp);
}
.content-middle .graphic-design h3, .content-middle .graphic-design p {
  color: #25564b;
}
.content-middle .photography {
  background-image: url(/build/img/desktop/image-photography.webp);
}
.content-middle .photography h3, .content-middle .photography p {
  color: #19536b;
}
@media (min-width: 992px) {
  .content-middle {
    grid-column: 1/3;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.header {
  background-image: url(/build/img/mobile/image-header.webp);
  background-size: cover;
  background-position: center bottom;
  background-repeat: no-repeat;
  padding: 3rem;
  padding-bottom: 20rem;
  color: white;
  height: 100%;
  width: 100%;
}
.header .logo {
  display: flex;
  align-self: center;
  width: auto;
  height: 5rem;
}
.header .arrow {
  display: flex;
  justify-content: center;
  margin: 10rem auto 0;
  height: 25rem;
  width: auto;
}
.header h1 {
  margin: 15rem auto 3rem auto;
  letter-spacing: 0.5rem;
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 5rem;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 992px) {
  .header {
    background-image: url(/build/img/desktop/image-header.webp);
    padding-bottom: 40rem;
  }
  .header h1 {
    font-size: 7rem;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  font-size: 2rem;
  font-weight: 600;
}
.nav .nav-bar {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 40%;
}
.nav .contact {
  color: black;
  text-transform: uppercase;
  font-family: "Fraunces", serif;
  font-size: 2rem;
  font-weight: 700;
  background-color: white;
  border-radius: 5rem;
  padding: 3rem;
}
.nav .contact:hover {
  background-color: #6ECFFF;
  color: white;
}

.testimonials-container h2 {
  grid-column: 1/6;
  color: #a7abae;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.8rem;
  margin: 6rem auto;
}
.testimonials-container .photos-container {
  margin-bottom: 4rem;
  border-radius: 100%;
  max-height: 10rem;
  width: auto;
}
.testimonials-container .first {
  grid-column: 2/3;
}
.testimonials-container .testimonial {
  text-align: center;
  width: 78%;
  margin: 0 auto;
}
.testimonials-container .testimonial .text {
  line-height: 3rem;
  color: #818498;
}
@media (min-width: 992px) {
  .testimonials-container {
    display: grid;
    grid-template-columns: 1fr repeat(3, 2fr) 1fr;
  }
  .testimonials-container h2 {
    margin: 14rem auto;
  }
}

.testimonial-data {
  margin-top: 3rem;
  margin-bottom: 6rem;
}
.testimonial-data .name {
  font-family: "Fraunces", serif;
  color: black;
}
.testimonial-data .job {
  color: #a7abae;
}
@media (min-width: 992px) {
  .testimonial-data {
    margin-top: 8rem;
    margin-bottom: 20rem;
  }
}

.images-bottom {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.images-bottom img {
  width: 100%;
  height: 100%;
}
@media (min-width: 992px) {
  .images-bottom {
    grid-template-columns: repeat(4, 1fr);
  }
}

.nav-bottom {
  background-color: #90D4C5;
  min-height: 37rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.nav-bottom .nav-bar-bottom {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}
.nav-bottom .nav-bar-bottom a {
  color: #19536b;
}
.nav-bottom img {
  max-width: 3rem;
  height: auto;
  width: auto;
}
.nav-bottom .logo {
  min-width: 20rem;
  margin-bottom: -4rem;
}
.nav-bottom .logos {
  display: flex;
  justify-content: space-evenly;
  width: 80%;
}
@media (min-width: 992px) {
  .nav-bottom .nav-bar-bottom {
    width: 25%;
  }
  .nav-bottom .logos {
    justify-content: space-between;
    width: 10%;
  }
}

.attribution {
  font-family: "Fraunces", serif;
  background-color: #90D4C5;
}
.attribution p {
  margin: 0;
}